"use client"

import { useEffect, useState } from "react"
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Calendar, Clock, Users, Award, ExternalLink } from "lucide-react"
import { useSubgraph } from "@/context/graphql/queries"
import { formatDistanceToNow } from "date-fns"
import { Skeleton } from "@/components/ui/skeleton"

export function ActiveCampaigns({
  searchTerm = "",
  sortOrder = "latest",
  filterType = "all",
  filterValue = null
}) {
  const { events, loading } = useSubgraph()
  const [filteredCampaigns, setFilteredCampaigns] = useState([])
  
  useEffect(() => {
    if (events?.campaignsCreated) {
      filterCampaigns()
    }
  }, [events, searchTerm, sortOrder, filterType, filterValue])
  
  const filterCampaigns = () => {
    if (!events?.campaignsCreated) return
    
    const now = Math.floor(Date.now() / 1000) // Current timestamp in seconds
    
    // First, get unique campaigns (in case of duplicates)
    const uniqueCampaigns = {}
    events.campaignsCreated.forEach(campaign => {
      uniqueCampaigns[campaign.campaignId] = campaign
    })
    
    let campaigns = Object.values(uniqueCampaigns)
    
    // Apply status filter
    if (filterType === "active") {
      campaigns = campaigns.filter(campaign => {
        const startTime = parseInt(campaign.startTime)
        const endTime = parseInt(campaign.endTime)
        return now >= startTime && now <= endTime
      })
    } else if (filterType === "upcoming") {
      campaigns = campaigns.filter(campaign => {
        const startTime = parseInt(campaign.startTime)
        return now < startTime
      })
    } else if (filterType === "ended") {
      campaigns = campaigns.filter(campaign => {
        const endTime = parseInt(campaign.endTime)
        return now > endTime
      })
    }
    
    // Apply search
    if (searchTerm) {
      const term = searchTerm.toLowerCase()
      campaigns = campaigns.filter(campaign => {
        // Search by creator address
        if (campaign.creator.toLowerCase().includes(term)) return true
        
        // Get candidate names for this campaign
        const candidates = events.candidateAdded?.filter(
          candidate => candidate.campaignId === campaign.campaignId
        ) || []
        
        // Check if any candidate name matches search
        return candidates.some(candidate => 
          candidate.name.toLowerCase().includes(term)
        )
      })
    }
    
    // Apply type filter if needed
    if (filterValue === "candidate") {
      campaigns = campaigns.filter(campaign => campaign.votingType === "0")
    } else if (filterValue === "proposal") {
      campaigns = campaigns.filter(campaign => campaign.votingType === "1")
    }
    
    // Sort campaigns
    campaigns = campaigns.sort((a, b) => {
      if (sortOrder === "latest") {
        return parseInt(b.blockTimestamp) - parseInt(a.blockTimestamp)
      } else {
        return parseInt(a.blockTimestamp) - parseInt(b.blockTimestamp)
      }
    })
    
    setFilteredCampaigns(campaigns)
  }
  
  const getCampaignStatus = (startTime, endTime) => {
    const now = Math.floor(Date.now() / 1000) // Current timestamp in seconds
    
    if (now < parseInt(startTime)) {
      return { status: "upcoming", color: "blue" }
    } else if (now > parseInt(endTime)) {
      return { status: "ended", color: "gray" }
    } else {
      return { status: "active", color: "green" }
    }
  }
  
  const getVotingTypeLabel = (type) => {
    return type === "0" ? "Candidate Based" : "Proposal Based"
  }
  
  const getRestrictionLabel = (type) => {
    switch (type) {
      case "0": return "Public"
      case "1": return "NFT Holders Only"
      case "2": return "Whitelist Only"
      default: return "Unknown"
    }
  }
  
  const formatDate = (timestamp) => {
    const date = new Date(parseInt(timestamp) * 1000)
    return date.toLocaleDateString()
  }
  
  const getTimeRemaining = (startTime, endTime) => {
    const now = Math.floor(Date.now() / 1000)
    
    if (now < parseInt(startTime)) {
      return `Starts ${formatDistanceToNow(new Date(parseInt(startTime) * 1000), { addSuffix: true })}`
    } else if (now > parseInt(endTime)) {
      return `Ended ${formatDistanceToNow(new Date(parseInt(endTime) * 1000), { addSuffix: false })} ago`
    } else {
      return `Ends ${formatDistanceToNow(new Date(parseInt(endTime) * 1000), { addSuffix: true })}`
    }
  }
  
  // Find candidates for a specific campaign
  const getCampaignCandidates = (campaignId) => {
    if (!events?.candidateAdded) return []
    
    return events.candidateAdded.filter(
      candidate => candidate.campaignId === campaignId
    )
  }
  
  // Get vote count for a candidate or proposal
  const getVoteCount = (campaignId, targetId) => {
    if (!events?.voteCast) return 0
    
    return events.voteCast.filter(
      vote => vote.campaignId === campaignId && vote.targetId === targetId
    ).length
  }
  
  // Get votes for a campaign
  const getCampaignVoteCount = (campaignId) => {
    if (!events?.voteCast) return 0
    
    return events.voteCast.filter(
      vote => vote.campaignId === campaignId
    ).length
  }

  // Renders skeleton loading UI
  const renderSkeletons = () => {
    return Array(3).fill(0).map((_, i) => (
      <Card key={`skeleton-${i}`} className="overflow-hidden transition-all duration-300 hover:shadow-md">
        <CardHeader className="pb-2">
          <div className="flex justify-between items-start">
            <Skeleton className="h-7 w-3/4" />
            <Skeleton className="h-6 w-24" />
          </div>
        </CardHeader>
        <CardContent className="pb-2">
          <div className="space-y-4">
            <div className="flex items-center gap-2">
              <Skeleton className="h-5 w-5 rounded-full" />
              <Skeleton className="h-5 w-1/3" />
            </div>
            <div className="flex items-center gap-2">
              <Skeleton className="h-5 w-5 rounded-full" />
              <Skeleton className="h-5 w-1/2" />
            </div>
            <div className="flex items-center gap-2">
              <Skeleton className="h-5 w-5 rounded-full" />
              <Skeleton className="h-5 w-2/5" />
            </div>
          </div>
        </CardContent>
        <CardFooter className="flex justify-between pt-2">
          <Skeleton className="h-10 w-24" />
          <Skeleton className="h-10 w-24" />
        </CardFooter>
      </Card>
    ))
  }

  if (loading) {
    return (
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {renderSkeletons()}
      </div>
    )
  }
  
  if (filteredCampaigns.length === 0) {
    return (
      <div className="flex flex-col items-center justify-center py-12 text-center">
        <div className="rounded-full p-3 bg-muted mb-4">
          <Calendar className="h-6 w-6 text-muted-foreground" />
        </div>
        <h3 className="text-xl font-semibold mb-2">No campaigns found</h3>
        <p className="text-muted-foreground max-w-md">
          {filterType !== "all" 
            ? `There are no ${filterType} campaigns matching your search criteria.` 
            : "There are no campaigns matching your search criteria."}
        </p>
      </div>
    )
  }
  
  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
      {filteredCampaigns.map((campaign) => {
        console.log(campaign)
        const { status, color } = getCampaignStatus(campaign.startTime, campaign.endTime)
        const candidates = getCampaignCandidates(campaign.campaignId)
        const voteCount = getCampaignVoteCount(campaign.campaignId)
        
        return (
          <Card key={campaign.id} className="overflow-hidden transition-all duration-300 hover:shadow-md">
            <CardHeader className="pb-2">
              <div className="flex justify-between items-start">
                <CardTitle className="text-xl truncate" title={`Campaign #${campaign.campaignId}`}>
                  {campaign.description}
                </CardTitle>
                <Badge variant="outline" className={`bg-${color}-500/10 text-${color}-500 border-${color}-200`}>
                  {status}
                </Badge>
              </div>
            </CardHeader>
            <CardContent className="pb-2">
              <div className="space-y-4 text-sm">
                <div className="flex items-center gap-2">
                  <Clock className="h-4 w-4 text-muted-foreground" />
                  <span>{getTimeRemaining(campaign.startTime, campaign.endTime)}</span>
                </div>
                <div className="flex items-center gap-2">
                  <Users className="h-4 w-4 text-muted-foreground" />
                  <span>{voteCount} vote{voteCount !== 1 ? 's' : ''} â€¢ {candidates.length} candidate{candidates.length !== 1 ? 's' : ''}</span>
                </div>
                <div className="flex items-center gap-2">
                  <Award className="h-4 w-4 text-muted-foreground" />
                  <div className="flex gap-2">
                    <Badge variant="secondary" className="rounded-sm">
                      {getVotingTypeLabel(campaign.votingType)}
                    </Badge>
                    <Badge variant="outline" className="rounded-sm">
                      {getRestrictionLabel(campaign.restriction)}
                    </Badge>
                  </div>
                </div>
              </div>
            </CardContent>
            <CardFooter className="flex justify-between pt-2">
              <Button variant="outline" size="sm">
                View Details
              </Button>
              <Button size="sm" className="gap-1">
                Participate <ExternalLink className="h-3 w-3 ml-1" />
              </Button>
            </CardFooter>
          </Card>
        )
      })}
    </div>
  )
}